import React, { Component } from 'react';
import { Link } from 'react-router';

import s from './Nav';

class Nav extends Component {
    constructor(props) {
        super(props);
        this.state = { // init menu
            display: false
        };
    }
    handleShowOrHide() {
        this.setState({ // change menu state
            display: !this.state.display
        });
    }
    render() {
        let cName = s.hide;
        if (this.state.display) { // change menu class
            cName = s.show;
        } else {
            cName = s.hide;
        }
        return (
            <div className={s.nav}>
                <Link to="/" className={s.home} />
                <Link to="/downlaod" className={s.download} />
                <button className={s.menu} onClick={() => this.handleShowOrHide()} />
                <div className={cName}>
                    <ul className={s.items}>
                        <li><Link to="/">首页</Link></li>
                        <li><Link to="/downlaod">下载</Link></li>
                        <li><Link to="/">天气服务</Link></li>
                        <li><Link to="/news">资讯</Link></li>
                        <li><Link to="/">关于墨迹</Link></li>
                    </ul>
                </div>
            </div>
        );
    }
}

export default Nav;



